import {
    AppstoreFilled,
    AppstoreOutlined
} from '@ant-design/icons';
import {Button, Layout, Menu, theme, Row, Col, List, Switch, Dropdown, Avatar} from 'antd';
import React, {useEffect, useState} from 'react';
import './layout.css';
import {history} from "../../helpers";
import {setDefaultLanguage, setLanguage, setLanguageCookie, setTranslations, translate} from "react-switch-lang";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import vi from "../../language/vi.json";
import en from "../../language/en.json";
import HomeComponent from "../home/home";
import FooterComponent from "../home/footer";

const {Header, Sider, Content} = Layout;

setTranslations({vi, en});

// If you want to remember selected language
setLanguageCookie();

const LayoutComponent = (props) => {
    const {t} = props;
    const [current, setCurrent] = useState('dashboard');
    const [open, setOpen] = useState(false);
    const [collapsed, setCollapsed] = useState(false);
    const [theme, setTheme] = useState('light');
    const [valueLanguage, setValueLanguage] = useState('vi');
    const [classNameHot, setClassNameHot] = useState(theme === 'light' ? 'light-theme' : 'dark-theme');

    function changeTheme(value) {
        setTheme(value ? 'dark' : 'light');
        setClassNameHot(value ? 'dark-theme' : 'light-theme');
    }

    function handleSetLanguage(key) {
        const lang = key ? 'en' : 'vi';
        setValueLanguage(lang);
        setLanguage(lang);
    }

    function signOut(e) {
        history.push('/login')
    }

    function handleMenuClick(e) {
        if (e.key === '3') {
            setOpen(false);
        }
    }

    function handleOpenChange(flag) {
        setOpen(flag);
    }

    useEffect(() => {
        if (theme === 'light') {
            document.body.classList.remove('dark');
            document.body.classList.add('light');
        } else {
            document.body.classList.remove('light');
            document.body.classList.add('dark');
        }
    }, [theme]);

    useEffect(() => {
        setDefaultLanguage(valueLanguage);
        const now = new Date();
        if (now.getHours() < 6 || now.getHours() >= 18) {
            setTheme('dark');
            setClassNameHot('dark-theme');

        } else {
            setTheme('light');
            setClassNameHot('light-theme');
        }
    }, []);


    const items = [
        {
            label: (
                <Row style={{padding: 5}}>
                    <Col span={12}>
                        {t('text.txt_language')}
                    </Col>
                    <Col span={12}>
                        <Switch defaultChecked={valueLanguage === 'en'}
                                onChange={handleSetLanguage}
                                checkedChildren="Eng"
                                unCheckedChildren="Viet"
                        />
                    </Col>
                </Row>
            ),
            key: 'language',
        },
        {
            label: (
                <Row style={{padding: 5}}>
                    <Col span={12}>
                        {t('text.txt_theme')}
                    </Col>
                    <Col span={12}>
                        <Switch
                            checked={theme === 'dark'}
                            onChange={changeTheme}
                            checkedChildren="Dark"
                            unCheckedChildren="Light"
                        />
                    </Col>
                </Row>
            ),
            key: 'theme',
        },
        {
            label: (
                <Row align={"left"} onClick={e => signOut(e)}>
                    {t('home.logout')}
                </Row>
            ),
            key: 'signout',
        },
    ];


    const itemsMenuHeader = [
        {
            label: t('home.title'),
            key: 'dashboard',
            icon: <AppstoreFilled/>,
        },
    ];

    const onClick = (e) => {
        console.log('click ', e);
        setCurrent(e.key);
    };


    return (
        <Layout hasSider style={{
            minHeight: '100vh',
        }}>
            <Layout className="site-layout">
                <Header
                    style={{
                        position: 'sticky',
                        top: 0,
                        zIndex: 1,
                        width: '100%',
                        alignItems: 'center'
                    }}
                >
                    <Row>
                        <Col span={3}>
                            <div className="demo-logo-vertical"/>
                        </Col>
                        <Col span={12}>
                            <Menu theme={theme} onClick={onClick} selectedKeys={[current]} mode="horizontal"
                                  items={itemsMenuHeader}/>
                        </Col>
                        <Col span={9}>
                            <Row justify="end">
                                <Dropdown
                                    overlayStyle={{width: 256}}
                                    menu={{
                                        items,
                                        onClick: handleMenuClick
                                    }}
                                    onOpenChange={handleOpenChange}
                                    open={open}
                                >
                                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
                                        <Avatar size="large"
                                                src={'https://scontent.fsgn13-2.fna.fbcdn.net/v/t39.30808-6/269870110_4627302557381096_845600942053273274_n.jpg?_nc_cat=109&ccb=1-7&_nc_sid=09cbfe&_nc_ohc=9driW8vCHm8AX81eiDD&_nc_ht=scontent.fsgn13-2.fna&oh=00_AfAtfTPxzNvCNyU8yvMiHrIbl3mEHczIBwdoQnm1cDWo7A&oe=646F07EB'}/>
                                    </a>
                                </Dropdown>
                            </Row>
                        </Col>
                    </Row>
                </Header>
                <Content style={{margin: '24px 16px 0', overflow: 'initial'}}>
                    <HomeComponent {...props} classNameHot={classNameHot} theme={theme} valueLanguage={valueLanguage}/>
                </Content>
                <FooterComponent />
            </Layout>
        </Layout>
    );
};

LayoutComponent.propTypes = {
    t: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
    const {alert, users, authentication} = state;
    const {user} = authentication;
    return {
        alert,
        users,
        user
    };
}

export default connect(mapStateToProps,
    null,
    null,
    {forwardRef: true}
)(translate(LayoutComponent));