import React, {useState} from "react";
import {Tabs} from 'antd';
import PropTypes from 'prop-types';
import {connect} from "react-redux";
import {translate} from 'react-switch-lang';
import {StickyContainer, Sticky} from 'react-sticky';
import IncomeComponent from "./incomes";
import IncomeHistoryComponent from "./income_history";
import SavingComponent from "../saving/saving";
import SavingHistoryComponent from "../saving/saving_history";
import ExpenseComponent from "../expense/expense";
import ExpenseHistoryComponent from "../expense/expense_history";

const {TabPane} = Tabs;
const IncomePage = (props) => {
    const {t} = props;

    const [activeKey, set_activeKey] = useState(1);

    const renderTabBar = (props, DefaultTabBar) => (
        <Sticky bottomOffset={80}>
            {({style}) => (
                <DefaultTabBar {...props} className="tab-1-custom-tab-bar" style={{...style}}/>
            )}
        </Sticky>
    );

    const onChange = (activeKey) => {
        set_activeKey(activeKey);
    }


    return (
        <div>
            <StickyContainer>
                <Tabs animated={true} onChange={onChange} defaultActiveKey={1} renderTabBar={renderTabBar}
                      items={[
                          {
                              key: 1,
                              label: (
                                  <span>
                                          <img style={{marginRight: 10, borderRadius: 20}}
                                               src={require('../../images/icons/income.jpg').default}
                                               alt={""} height={40}/>
                                      {t('income.name')}
                                  </span>
                              ),
                              children: (
                                  <IncomeComponent activeKey={activeKey} {...props}/>
                              ),
                          },
                          {
                              key: 2,
                              label: (
                                  <span>
                                      <img style={{marginRight: 10, borderRadius: 20}}
                                           src={require('../../images/icons/income.jpg').default}
                                           alt={""} height={30}/>
                                      {t('income.history')}
                                  </span>
                              ),
                              children: (<IncomeHistoryComponent activeKey={activeKey} {...props}/>),
                          },
                          {
                              label: null,
                              key: 'null_1',
                              children: null,
                              disabled: true,
                          },
                          {

                              key: 3 ,
                              label: (
                                  <span>
                                  <img style={{marginRight: 10, borderRadius: 20}}
                                       src={require('../../images/icons/expense.png').default}
                                       alt={""} height={40}/>
                                      {t('expense.name')}
                                </span>
                              ),
                              children: (<ExpenseComponent activeKey={activeKey} {...props}/>),
                          },
                          {
                              key: 4,
                              label: (
                                  <span>
                                  <img style={{marginRight: 10, borderRadius: 20}}
                                       src={require('../../images/icons/expense.png').default}
                                       alt={""} height={30}/>
                                      {t('expense.history')}
                                </span>
                              ),
                              children: (<ExpenseHistoryComponent activeKey={activeKey} {...props}/>),
                          },
                          {
                              label: null,
                              key: 'null_2',
                              children: null,
                              disabled: true,
                          },
                          {
                              key: 5,
                              label: (
                                  <span>
                                      <img style={{marginRight: 10, borderRadius: 20}}
                                           src={require('../../images/icons/saving.png').default}
                                           alt={""} height={40}/>
                                      {t('saving.name')}
                                  </span>
                              ),
                              children: (<SavingComponent activeKey={activeKey} {...props}/>),
                          },
                          {
                              key: 6,
                              label: (
                                  <span>
                                      <img style={{marginRight: 10, borderRadius: 20}}
                                           src={require('../../images/icons/saving.png').default}
                                           alt={""} height={30}/>
                                      {t('saving.history')}
                                  </span>
                              ),
                              children: (<SavingHistoryComponent activeKey={activeKey} {...props}/>),
                          }
                      ]
                      }>
                </Tabs>
            </StickyContainer>
        </div>
    )
}
IncomePage.propTypes = {
    t: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
    const {users, authentication} = state;
    const {user} = authentication;
    return {
        user,
        users,
    };
}

export default connect(
    mapStateToProps,
    null,
    null,
    {forwardRef: true}
)(translate(IncomePage));
